<template>
  <div class="upload">
    <div class="data-component">
      <div class="box">
        <b-form-file
          v-model="logFile"
          :state="Boolean(logFile)"
          class="log-file"
          placeholder="Choose a file or drop it here..."
          drop-placeholder="Drop file here..."
          size="sm"
        ></b-form-file>

        <div class="sub-content">
          <ul class="log-manual" v-if="!Boolean(logFile)">
            <li>BASE_PATH <i>(show hidden folder)</i> : C:\Users\user_name\AppData\Local\Blizzard\Hearthstone</li>
            <li>
              1. log.config modify
              <ul>
                <li><span class="command">Find</span><br/>[Power]<br/>...<br/>ConsolePrinting=<b>False</b><br/>...</li>
                <li><span class="command">Replace with</span></li>
                <li>[Power]<br/>...<br/>ConsolePrinting=<b>True</b><br/>...</li>
                <li><span class="command">Save</span></li>
              </ul>
            </li>
            <li>2. Hearthstone restart</li>
            <li>3. After game, upload log file</li>
          </ul>

          <ul v-else>
            <li v-for="el in logContent" :key="el">{{ el }}</li>
          </ul>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
//import Present from "@/components/Present.vue";

export default {
  name: "upload",
  components: {
    //Present
  },
  data: () => {
    return {
      logFile: null,
      logContent: []
    }
  },
  watch: {
    logFile (newVal) {
      this.readFile(newVal)
    }
  },
  methods: {
    readFile (file) {
      let reader = new FileReader();
      reader.onload = () => {
        let text = reader.result;
        console.log(text)
        //let node = document;
      }
      reader.readAsText(file);
    }
  }
};
</script>

